import React from 'react';
import { Section } from 'react-scroll-section';
import { Heading } from 'rebass';
import tw from 'tailwind.macro'
import styled from 'styled-components';
import PropTypes from 'prop-types';
import Slide from 'react-reveal/Slide';
import LinkAnimated from './LinkAnimated';

const SectionContainer = styled.div`
  background: ${props => (props.background ? props.background : '#fff')};
  /* min-height: 100vh; */
  /* min-width: 320px;
  max-width: 1000px; */
  display: flex;
  margin: auto;
  flex: 0 1 auto;
  flex-direction: column;
  justify-content: center;
  /* padding: 3rem 7.5vw 3rem; */
  padding: 0rem 7.5vw 7rem;
  scroll-behavior: smooth;
  z-index: 99;
  &.full {
    min-height: 100vh;
    padding: 5em 7.5vw;
  }
  &.alt {
    background: #F1EDF7;
    h2 span {
      border-bottom-color: transparent;
    }
  }
  &.first {
    padding-top: 4rem;
  }
  &.last {
    padding-bottom: 6rem;
  }
  div div {
    z-index: 9;
  }
`

const Wrapper = styled.div`
  ${tw`w-full max-w-xl m-auto z-10`}
  /* min-width: 400px; */
  /* max-width: 1000px; */
`

const Title = styled(Heading)`
  z-index: 99;
`

const Container = ({ id, children, background, className }) => (
  <Section id={id} style={{ position: 'relative' }}>
    <SectionContainer background={background} className={className}>
      <Wrapper>{children}</Wrapper>
    </SectionContainer>
  </Section>
)

Container.defaultProps = {
  background: '#ffffff',
}
Container.propTypes = {
  id: PropTypes.string.isRequired,
  children: PropTypes.node.isRequired,
  background: PropTypes.string,
  className: PropTypes.string,
};

const Header = ({ name, icon = '', label = '' }) => (
  <Slide left>
    <Title color="primaryDark" as="h2" fontSize={[4, 4, 6]} mb={4} zIndex={99}>
      <LinkAnimated selected>
        {icon && (
          <span role="img" aria-label={label} style={{ marginRight: '10px' }}>
            {icon}
          </span>
        )}
        {name}
      </LinkAnimated>
    </Title>
  </Slide>
)

Header.propTypes = {
  name: PropTypes.string.isRequired,
  icon: PropTypes.string,
  label: PropTypes.string,
}

export default {
  Container,
  Header,
}
